.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #f0f0f0;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.textField {
  width: 400px;
  padding: 20px;
}

.result-container {
  /* width: 300px; */
  margin: 0 auto;
  display: flex;
  flex-direction: column;
}

.column {
  display: flex;
  flex-direction: column;
  width: 300px;
  justify-content: space-between;
  color: #646464;
}

.row {
  display: flex;
  justify-content: space-between;
}